<template>
  <v-container>
    <v-row v-if="!loading">
      <v-col class="text-center" v-if="plans.length == 0">
           
      </v-col>
      <v-col
        v-motion
        :initial="{ opacity: 0, x: 100 }"
        :enter="{
          opacity: 1,
          x: 0,
          scale: 1,
          transition: {
            duration: 500,
          },
        }"
        :variants="{ custom: { scale: 2 } }"
        :delay="500"
        cols="12"
        lg="12"
        md="12"
        sm="12"
        xs="12"
        class=""
      >
        <v-text-field
          v-model="searchText"
          theme="light"
          variant="solo"
          prepend-inner-icon="mdi-magnify"
          placeholder=" "
          density="comfortable"
          color="primary"
          clearable
          rounded="xl"
        ></v-text-field>
      </v-col>
      <v-col
        v-for="plan in filteredPlans"
        :key="plan.id"
        cols="12"
        lg="4"
        md="4"
        sm="12"
        xs="12"
      >
        <v-hover>
          <template v-slot:default="{ isHovering, props }">
            <v-card
              height="600"
              rounded="lg"
              v-bind="props"
              :color="isHovering ? 'primary' : undefined"
              elevation="2"
              :loading="loading"
              class="mx-auto"
            >
              <template v-slot:loader="{ isActive }">
                <v-progress-linear
                  :active="isActive"
                  color="primary"
                  height="4"
                  indeterminate
                ></v-progress-linear>
              </template>

              <v-img cover height="250" :src="plan.imageUrl"></v-img>

              <v-card-item>
                <v-card-title>{{ plan.title }}</v-card-title>

                <v-card-subtitle>
                  <span class="me-1"
                    >{{ plan.places[0].address.country }} ,
                    {{ plan.places[0].address.city }}</span
                  >

                  <v-icon
                    color="error"
                    icon="mdi-fire-circle"
                    size="small"
                  ></v-icon>
                </v-card-subtitle>
              </v-card-item>

              <v-card-text>
                <v-row align="center" class="mx-0">
                  <v-rating
                    :model-value="4.5"
                    color="amber"
                    density="compact"
                    half-increments
                    readonly
                    size="small"
                  ></v-rating>

                  <div class="text-grey ms-4">4.5 (413)</div>
                </v-row>

                <div class="my-4 text-subtitle-1">
                  {{ plan.price }}   
                </div>

                <div>
                  

                  {{ plan.dateFrom }}
                  
                  {{ plan.dateTo }}
                  <p class="pb-2">  : {{ plan.memberNumber }}</p>
                  <p :class="isHovering ? '' : 'text-grey'">
                    {{ plan.description }}
                  </p>
                </div>
              </v-card-text>

              <v-card-actions>
                <v-btn
                  :color="isHovering ? undefined : 'primary'"
                  variant="text"
                  :to="'travelDetailsHome/' + plan.id"
                >
                  
                </v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-hover>
      </v-col>
      <v-col cols="12" lg="12" md="12" sm="12" xs="12">
        <div class="text-center">
          <v-pagination
            rounded="circle"
            v-model="page"
            :length="1"
            :total-visible="3"
          ></v-pagination></div
      ></v-col>
    </v-row>
    <div class="loading-container" v-if="loading">
      <v-progress-circular
        :size="70"
        color="primary"
        indeterminate
      ></v-progress-circular>
    </div>
  </v-container>
</template>
<script setup>
import { onMounted, ref, computed } from "vue";
import { db } from "../../../firebase.js";
import { getDocs, collection } from "firebase/firestore";

const loading = ref(true);
const plans = ref([]);
const page = ref(1);
const searchText = ref("");

onMounted(async () => {
  try {
    const planCollection = await getDocs(collection(db, "travelPlans"));

    const promises = [];

    planCollection.forEach((plan) => {
      promises.push(
        new Promise((resolve) => {
          plans.value.push({ ...plan.data(), id: plan.id });
          resolve();
        })
      );
    });

    await Promise.all(promises);

    loading.value = false;
  } catch (error) {
    loading.value = false;
  }
});

const filteredPlans = computed(() => {
  return plans.value.filter((plan) =>
    plan.title.toLowerCase().includes(searchText.value.toLowerCase())
  );
});
</script>
<style>
.loading-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}
</style>
