<template>
  <v-app-bar
    app
    :elevation="elevation"
    :class="
      store.isDark == true || store.isDark == 'true'
        ? 'appBarClassDark'
        : 'appBarClass'
    "
    class=""
  >
    <div aria-label="headerpage">
      <v-img
        v-motion-pop
        :width="mdAndUp ? '50' : '50'"
        class="pt-4 mr-6"
        alt="logo2"
        src="../../../assets/logo.png"
      >
      </v-img>
    </div>
    <v-spacer></v-spacer>

  

    <v-btn to="/login" color="primary" variant="text"></v-btn>

  
  </v-app-bar>
</template>

<script>

import { useTheme, useDisplay } from "vuetify";
import { useCounterStore } from "@/store/index.js";
import SweetScroll from "sweet-scroll";
export default {
  setup() {
    const theme = useTheme();
    const { mdAndUp, xlAndUp } = useDisplay();
    const store = useCounterStore();
    function toggleTheme() {
      if (theme.global.name.value == "dark") {
        theme.global.name.value = "light";
        store.isDark = false;
        localStorage.setItem("isDark", false);
      } else {
        theme.global.name.value = "dark";
        store.isDark = true;
        localStorage.setItem("isDark", true);
      }
    }

    return {
      mdAndUp,
      store,
      xlAndUp,
      toggleTheme,
      theme,
    };
  },
  data() {
    return {
      isScrolled: false,
      elevation: 0,
      activeItemIndex: 0,
    };
  },
  computed: {
    appBarClass() {
      return {
        primary: this.isScrolled,
        transparent: !this.isScrolled,
      };
    },
    appBarClassDark() {
      return {
        primary: this.isScrolled,
        transparent: !this.isScrolled,
      };
    },
  },

  methods: {
    chanageLang() {
      if (this.$i18n.locale == "ar") {
        this.$i18n.locale = "en";
        this.$vuetify.locale.rtl.ar = false;
      } else {
        this.$i18n.locale = "ar";
        this.$vuetify.locale.rtl.ar = true;
      }

      localStorage.setItem("lang", this.$i18n.locale);
    },

    scrollToSection(index) {
      const section = document.getElementById(`section${index}`);
      if (section) {
        const sweetScroll = new SweetScroll();
        sweetScroll.to(`#section${index}`, {
          easing: "easeInOutCubic",
          offset: -100,
        });

        // window.scrollTo({ top: section.offsetTop, behavior: "smooth" });
      }
      this.activeItemIndex = index;
    },
  },
};
</script>
<style scoped>
.v-application--is-scrolled .v-app-bar {
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
}
.green--text {
  color: green !important;
}
.appBarClass {
  background-color: rgba(
    255,
    255,
    255,
    0.5
  ); /* set a semi-transparent background color */
  backdrop-filter: blur(4px);
}
.appBarClassDark {
  background-color: rgba(
    5,
    12,
    26,
    0.5
  ); /* set a semi-transparent background color */
  backdrop-filter: blur(4px);
}
</style>
